import { connect, disconnect, receiveMessage } from './webSocketSlice';
import type { AppDispatch } from '../../store/index';

import webSocketConfig from './config.json';

/**
 * websocket.ts
 * WebSocket  ,     
 */

let socket: WebSocket | null = null;

// Map to store pending requests
const pendingRequests = new Map<string, (response: any) => void>();

// WebSocket  
export const initWebSocket = (dispatch: AppDispatch) => {
  return new Promise((resolve, reject) => {
    socket = new WebSocket(
      `ws://${webSocketConfig.server.host}:${webSocketConfig.server.port}/ws`,
    );

    // socket  store connect action dispatch
    socket.onopen = () => {
      dispatch(connect());
      resolve(0);
    };

    socket.onerror = (error) => {
      reject(error);
    };

    // socket  store disconnect action dispatch
    socket.onclose = () => {
      dispatch(disconnect());
    };

    // socket   store receiveMessage action dispatch
    socket.onmessage = (event: MessageEvent) => {
      const data = JSON.parse(event.data);

      if (data.requestId && pendingRequests.has(data.requestId)) {
        // Resolve the pending request
        pendingRequests.get(data.requestId)!(data);
        pendingRequests.delete(data.requestId);
      } else {
        dispatch(receiveMessage(data));
      }
    };
  });
};

// Generate a unique ID for each request
const generateUniqueId = (): string => {
  return Math.random().toString(36).substring(2, 15);
};

//   
export const sendWebSocketMessage = (message: any): Promise<any> => {
  return new Promise((resolve, reject) => {
    if (socket && socket.readyState === WebSocket.OPEN) {
      const requestId = generateUniqueId();

      pendingRequests.set(requestId, resolve);
      socket.send(JSON.stringify({ ...message, requestId }));
    } else {
      reject(new Error('WebSocket is not open'));
    }
  });
};
